import Vue from 'vue';
import Vuex from 'vuex';
import api from '../api';

Vue.use(Vuex);

export default new Vuex.Store ({
	state: {
		todos:[{
			id: 1,
			label: 'ce que je veux'
		}],
		task:[{
			name: ''
		}]	
	},
	
	getters: {
		count(state) {
			return state.todos.length;
		},
		todos(state){
			return state.todos;
		},
		add(state) {
			return state.task;
		},

	},
	mutations: {
		POPULATE_TODOS(state, todos){
			state.todos = todos;
		},
		NEW_TODOS(state, task){
			state.task = task;
		},
	},
	actions: {
		getTodos(context){
			api.get(`/todos`)
		      .then((response) => {
		      	context.commit('POPULATE_TODOS', response.data);
		      })
		      .catch((e) => {
		        this.errors.push(e);
		      });
			},
		postTodos(context){
			api.post('/todos', {
				body: context.commit('NEW_TODOS', response.data)
			})
		 	.then(response => {})
			.catch(e => {
  				this.errors.push(e)
				})
			}
		},
		
});


