<template>
      <div class="col-md-6" v-show="menu"  >
          <div class="form-group has-feedback" v-for="tag in add">
            <br>
              <label>{{ tag.name }}:</label>
                 <p class="control has-icon has-icon-right">
                    <input name="tache" v-model="tag.name" v-validate="{ required: true, regex: /^[A-Za-z0-9\-_\.\s]*$/}"
                     :class="{'has_errors': errors.has('tache') }"  class="form-control">
                      <span v-show="errors.has('tache')" class="is-danger">
                         <i class="fa fa-times"></i>
                            {{ errors.first('tache') }}
                      </span>
                </p>
          </div>
        <div class="col-md-2">
          <div class="form-group has-feedback">
            <button class="btn btn-primary btn-lg" @click="NEW_TODOS()">Ajouter</button>
          </div>
        </div>
      </div>
</template>

<script>

import api from '../api';
import Vue from 'vue'
import Multiselect from 'vue-multiselect';
import VeeValidate from 'vee-validate';
Vue.component('select2', Multiselect);


const config = {
    errorBagName: 'errors', // change if property conflicts
    fieldsBagName: 'fields',
    delay: 0,
    locale: 'en',
    dictionary: null,
    strict: true,
    classes: false,
    classNames: {
        touched: 'touched', // the control has been blurred
        untouched: 'untouched', // the control hasn't been blurred
        valid: 'valid', // model is valid
        invalid: 'invalid', // model is invalid
        pristine: 'pristine', // control has not been interacted with
        dirty: 'dirty' // control has been interacted with
    },
    events: 'input|blur',
    inject: true,
    validity: false,
    aria: true,
    i18n: null, // the vue-i18n plugin instance,
    i18nRootKey: 'validations' // the nested key under which the validation messsages will be located
};
Vue.use(VeeValidate, config);

export default {
  name: `Input`,
  props: {
    menu: {
        type: Boolean,
        default: false
    },
  },
  data() {
    return {
    };
  },
  computed: {
    add(){
      return this.$store.getters.add;
    },

  },
  methods: {
    NEW_TODOS: function () {
      this.$store.commit('NEW_TODOS')
      this.$store.dispatch("postTodos")
    }
  }
  
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  // display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
input{
  border-radius: 10px;
}
.is-danger {
    color: #9f191f;
    font-weight: bold;
}
.col-md-6
{
  display: inline-block;

}
.col-md-2 {
  display: inline-block;
}

</style>



