<template>
  <div id="app">
    <img src="./assets/list1.png">
    <lp-Header/>
    <router-view/>
    <button class="btn btn-primary" @click="menu = !menu">
            Ajouter une tâche
    </button>
    <Input
    :menu="menu">
    </Input>
  </div>
</template>

<script>

import lpHeader from './components/LpHeader'
import Input from './components/Input'

export default {
  name: `App`,
  created(){
    this.$store.dispatch("getTodos")
    //this.$store.dispatch("postTodos")
  },

  components: {
    lpHeader, Input
  },
  data() {
    return {
        menu: false,
      }
    },
 
  


  
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
},
.img {
  width: 300px;
  display: none;
}
</style>



